import React, { useState } from "react"

import Styles from "./AddProfile.module.css"
import Overlay from "./../../App.module.css"

import Form from "./../../molecules/Form/Form"

const AddProfile = ({updateData}) => {
    const [opened, setOpened] = useState(false)
    const props = {heading: "–î–æ–±–∞–≤–∏—Ç—å –∫–ª–∏–µ–Ω—Ç–∞", API: "/api/profile/add"}
    const blocks = [
        {id: 1, type: "text", name: "name", label: "–ò–º—è"},
        {id: 2, type: "text", name: "specific", label: "–î–æ–ø. –∏–Ω—Ñ–æ(–Ω–µ–æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ)"}
    ]

    const openForm = () => {
        setOpened(true)
    }

    const closeForm = () => {
        setOpened(false)
    }

    return(
        <div className={Styles.add}>
            <div className={`${Overlay.overlay} ${opened ? Styles.active : !opened}`} onClick={() => closeForm()}></div>
            { opened ? <Form updateData={updateData} props={props} blocks={blocks} closeForm={closeForm} /> : <div></div> }
            <a href="/" className={Styles.link} onClick={e => {e.preventDefault(); openForm()} }>
                –î–æ–±–∞–≤–∏—Ç—å
            </a>
        </div>
    )
}

export default AddProfile