import React from "react"
import Styles from "./Profile.module.css"
import Buttons from "./../../atoms/Buttons/Buttons"

const Profile = ({updateData, data}) => {
    const { date, name, specific, balance, _id } = data

    return(
        <div className={Styles.profile}>
            <div className={Styles.info}>
                <span>
                    {`–î–∞—Ç–∞ –∏–∑–º–µ–Ω–µ–Ω–∏—è: ${new Date(date).toLocaleString()}`}
                </span>
                <h3>
                    {`–ò–º—è: ${name}`} <br/>
                    {`–î–æ–ø. –∏–Ω—Ñ–æ: ${specific || "---"}`}
                </h3>
                <h2>
                    {`–ë–∞–ª–∞–Ω—Å: ${balance + ".00"}`}
                </h2>
            </div>
            <Buttons updateData={updateData} currentBalance={balance} _id={_id} name={name} />
        </div>
    )
}

export default Profile